import React, { Component } from 'react';
import {
  incrementQuantity,
  decrementQuantity,
  setRegistryProducts,
  setCheckListItems,
} from "../../../my-registry/MyRegistryActions";
import CardItem from "modules/commons/components/card-item/CardItem";
import { connect } from "react-redux";
import "./ViewRecommend.scss";
import MoreOption from "../more-option/MoreOption";
import { ADD_REGISTRY_ITEM, GET_PRODUCTS, GET_CATEGORIES, UPDATE_REGISTRY_ITEM } from 'api/queries';
import { graphQL } from 'api/axios';
import Slider from "react-slick";
// Import css files
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

class ViewRecommend extends Component {

  getProducts = () => {
    let data = {
      query: GET_PRODUCTS,
      variables: {
        registryId: parseInt(this.props.stateRegistryInfo.id)
      }
    }
    graphQL(data).then(res => {
      let responseData = res.data.data.registryItems.edges.map(item => {
        return { ...item.node }
      })
      this.props.dispatch(setRegistryProducts(responseData))

    }).catch(err => {
      console.error(err.message)
    })
  }

  getCategories = () => {
    let data = {
      query: GET_CATEGORIES,
      variables: {
        registryId: parseInt(this.props.stateRegistryInfo.id)
      },
    }
    graphQL(data).then(res => {
      let parentCategories = res.data.data.categories.filter(category => category.totalItemsCount > 0)
      let finalCategories = []
      finalCategories = parentCategories.map(parentCategory => {
        let subCategory = parentCategory.subCategories
        delete parentCategory.subCategories
        let newSubCategories = subCategory.filter(cat => cat.totalItemsCount > 0)
        parentCategory['subCategories'] = newSubCategories
        return parentCategory
      })
      this.props.dispatch(setCheckListItems(finalCategories))

    }).catch(err => {
      console.error(err.message)
    })
  }

  moreOptionsData = [
    // TODO add option list
    {
      name: "View Item",
      action: () => alert("Clicked")
    },
  ]
  getCategoryId = () => {
    const currentRecommendation = this.props.stateActiveRecommandation;
    for (let parentCategory of this.props.stateChecklist) {
      if (parentCategory.categoryName === currentRecommendation) {
        return parentCategory.id
      }
      for (let childCategory of parentCategory.subCategories) {
        if (childCategory.categoryName === currentRecommendation) {
          return childCategory.id
        }
      }
    }
  }

  addProductHandler = (card) => {
    let existingProduct = this.props.stateAddedProducts.filter(product => product.item.sku === card.variants.edges[0].node.sku)
    if (existingProduct.length === 0) {
      const registryId = parseInt(this.props.stateRegistryInfo.id)
      const title = card.title
      const category = card.category.id
      const storeItemUrl = card.onlineStoreUrl
      const sku = card.variants.edges[0].node.sku
      const price = parseFloat(card.variants.edges[0].node.price)
      const quantity = parseInt(card.quantity)
      const shopifyItemId = card.id
      const imageUrl = card.variants.edges[0].node.image.originalSrc

      let data = {
        query: ADD_REGISTRY_ITEM,
        variables: {
          registryId: registryId,
          title: title,
          category: category,
          tags: [],
          storeItemUrl: storeItemUrl ? storeItemUrl : '',
          sku: sku,
          price: price,
          quantity: quantity,
          shopifyItemId: shopifyItemId,
          imageUrl: imageUrl,
        }
      }
      graphQL(data).then(res => {
        this.getProducts()
        this.getCategories()
      }).catch(err => {
        console.error(err.message)
      })
    } else {
      let data = {
        query: UPDATE_REGISTRY_ITEM,
        variables: {
          registryItemId: existingProduct[0].id,
          quantity: parseInt(card.quantity) + parseInt(existingProduct[0].quantity),
        }
      }
      graphQL(data).then(res => {
        this.getProducts()
        this.getCategories()
      }).catch(err => {
        console.error(err.message)
      })
    }
  }

  render() {
    const settings = {
      dots: true,
      infinite: false,
      speed: 500,
      slidesToShow: 3.2,
      slidesToScroll: 1,
      prevArrow: <button type="button" class="slick-prev slick-prev-btn"><i className="icon-chevron-left slick-icon" /></button>,
      nextArrow: <button type="button" class="slick-next slick-next-btn"><i className="icon-chevron-right slick-icon" /></button>
    };
    return (
      <div className="recommended-cart my-6">
        <div className="row">
          <div className="col title"><h2>{this.props.title}
            {/* <span className="text-sm sub-title">Recommended </span> */}
          </h2>
          </div>
          <div className="col ml-auto">
            {/* <MoreOption data={this.moreOptionsData} iconClass="icon-more-hori"/> */}
            <button className="add-button text-body font-weight-bold py-1 px-3 float-right m-a-popover m-a-popover-top-right">
              Add on my own
              <div className="m-a-popover-content px-3 py-2">
                <span className="font-medium text-sm">What is Add My Own?</span>
                <p className="text-sm font-weight-normal m-a-popover-content-desc">
                  You can add links from other online store and save in your registry.
                </p>
              </div>
            </button>
          </div>
        </div>
        <div className="pt-6">
          <Slider {...settings}>
            {this.props.products.map((card, index) =>
              // <div><>{index}</></div>
              <div className="cart-view">
                <CardItem
                  className="mr-4"
                  data={card}
                  key={"CardItem-" + index}
                  incrementQuantityHandler={() => this.props.dispatch(incrementQuantity(card.id, 1))}
                  decrementQuantityHandler={() => this.props.dispatch(decrementQuantity(card.id, 1))}
                  addProductHandler={() => this.addProductHandler(card)}
                />
              </div>
            )}
          </Slider>
        </div>
        <div className="mt-6 card-buttom d-flex">
          {/* TODO: Change text to dynamic */}
          <div>
            <img className="blog-img" src="https://cdn.shopify.com/s/files/1/2338/2917/files/maedit_submenu.jpg?v=1588207400" alt={this.props.title}></img>
          </div>
          <div className="pl-4 my-auto">
            <div className="text-body">How to pick up a perfect crib mattress?</div>
            <div className="card-buttom-subtext text-sm">by Joanna Lee, March 13, 2020</div>
          </div>
          <div className="text-sm my-auto mr-6 ml-auto">
            <a href="" className="read-more">Read more</a>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    stateAddedProducts: state.myRegistry.addedProducts,
    stateActiveRecommandation: state.myRegistry.activeRecommandation,
    stateChecklist: state.myRegistry.checkList,
    stateRegistryInfo: state.registry.info
  }
}

export default connect(mapStateToProps)(ViewRecommend)