import React from 'react'
import {NavLink, Switch, Route, Redirect} from 'react-router-dom';
import MyRegistry from '../my-registry/MyRegistry';
import ViewRegistry from "../view-registry/ViewRegistry";
import {useTranslation} from "react-i18next";
import route from "utils/routes";
import RegistryHeroData from "data/RegistryHero.json";
import './RegistryHero.scss';
import {Navbar, Nav} from 'react-bootstrap';

const RegistryHero = (props) => {

  // "props.registryInfo" will contain all registry information

  const {t} = useTranslation();
  const registryData = RegistryHeroData
  return (
    <>
      <div className="registry-hero">
        <div className="container">

          {/* Registry Navbar */}
          <div className="d-flex pt-6 registry-hero-header">

            {/* Registry Info */}
            <div className="mr-3 mb-0"><h1>{props.registryInfo.name}</h1></div>
            <div className="d-flex mt-auto registry-hero-subtitle">
              <div>{t("registryHeader.subTitle")} {registryData.babyArrival}</div>
              {/* <div>| Partner: {registryData.partner}</div> */}
            </div>
          </div>

          {/* Adding Navbar */}
          <div className="d-flex m-0">
            <Navbar expand="lg" className="p-0">
              <Navbar.Toggle aria-controls="basic-navbar-na" className="pb-2"/>
              <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto">
                  <NavLink to={route.MY_REGISTRY.HOME} className="registry-header-link">
                    <h4>{t("registryHeader.link.myRegistry")}</h4></NavLink>
                  <NavLink to={route.VIEW_REGISTRY.HOME} className="registry-header-link">
                    <h4>{t("registryHeader.link.viewRegistry")}</h4></NavLink>
                  <NavLink to={route.MY_REGISTRY.TRACK_GIFTS} className="registry-header-link">
                    <h4>{t("registryHeader.link.trackGifts")}</h4></NavLink>
                  <NavLink to={route.MY_REGISTRY.SHOWER} className="registry-header-link">
                    <h4>{t("registryHeader.link.shower")}</h4></NavLink>
                  <NavLink to={route.MY_REGISTRY.ANNOUNCEMENTS} className="registry-header-link">
                    <h4>{t("registryHeader.link.announcements")}</h4></NavLink>
                </Nav>
              </Navbar.Collapse>
            </Navbar>
            <NavLink to={route.MY_REGISTRY.TO_DOS} className="ml-auto registry-header-link">
              <h4>{t("registryHeader.link.toDos")}</h4></NavLink>
          </div>
        </div>
      </div>
      {/* Registry Nav Routes */}
        <Switch>
          <Route exact path={route.REGISTRY.HOME}><Redirect to={route.MY_REGISTRY.HOME}/></Route>
          <Route exact path={route.MY_REGISTRY.HOME} component={MyRegistry}/>
          <Route exact path={route.VIEW_REGISTRY.HOME} component={ViewRegistry}/>
        </Switch>
    </>
  )
}

export default RegistryHero;