import React from "react";
import {Route, Switch} from "react-router-dom";
import route from "utils/routes";
import {connect} from "react-redux";
import RegistryHero from '../registry-hero/RegistryHero';
import VerifyToken from '../Login/VerifyToken/VerifyToken'
import {updateRegistryDetail} from "utils/commonFunction";

class ViewsContainer extends React.Component {

  componentDidMount() {
    updateRegistryDetail(this.props.dispatch);
  }

  render() {
    return (
      <>
        {/*<div>*/}
        {/*  <Link to="" style={{ marginRight: "30px" }}>Home</Link>*/}
        {/*  <Link to={route.REGISTRY.HOME}>Registry</Link>*/}
        {/*</div>*/}

        <Switch>
          {/*<Route exact path="/" component={Home} />*/}
          {/*<Route path="/another" component={Another} />*/}
          <Route path={route.REGISTRY.HOME} component={this.props.stateRegistryInfo.id ? () => <RegistryHero registryInfo={this.props.stateRegistryInfo}/> : null} />
          <Route path={route.LOGIN.VERIFY_TOKEN} component={VerifyToken}/>
          <Route render={() => <div>DEFAULT</div>}/>
        </Switch>
      </>
    )
  }
}

const mapStateToProps = state => {
  return {
    stateRegistryInfo: state.registry.info
  }
}

export default connect(
  mapStateToProps
)(ViewsContainer);
