import React from 'react';
import "./MoreOption.scss";

const MoreOption = (props) => {
  const ref = React.createRef();
  const optionButtonRef = React.createRef();

  const showDropdown = () => {
    ref.current.classList.toggle("show");
    optionButtonRef.current.classList.toggle(props.activeOptionClass ? props.activeOptionClass : "active-option-button");
  }

  return (
    <div className={["more-option", props.className ? props.className : null].join(" ")}>
      <button ref={optionButtonRef} className={["more-option-dropbtn my-auto", props.iconClass, props.buttonClass ? props.buttonClass : ""].join(" ")}
              onClick={showDropdown}/>
      <div className={["more-option-dropdown"].join(" ")} ref={ref}>
        <div className="more-option-content py-1">
          <ul className="p-0 m-0">
            {props.data ?
              props.data.map((option, index) =>
                <li className="py-2 px-4 text-sm" key={"option-" + index}>
                  <button className="more-option-item" onClick={() => {
                    showDropdown();
                    option.action();
                  }}>{option.name}</button>
                </li>
              )
              : null}
          </ul>
        </div>
      </div>
    </div>
  );
}

export default MoreOption;