import React from 'react';
import "./ModalComponent.scss";
import {Modal} from 'react-bootstrap';

const ModalComponent = (props) => {
  return (
    <Modal show={props.show} size={props.size ? props.size : "xl"} className="m-a-modal">
      <Modal.Header className="px-3 pt-3 pb-1 d-block">
        <div>
          <div className="d-flex justify-content-between">
            <h1>{props.title}</h1>
            <i className="icon-close m-a-modal-close align-self-center" onClick={props.closeModal}/>
          </div>
        </div>

        {/* OPTIONAL CONTENT IN TITLE PORTION */}
        {props.titleContent ? props.titleContent : null}

      </Modal.Header>
      <Modal.Body className="p-3 m-a-modal-content">
        {/*<div className="row">*/}
        {/*  <div className="col">*/}
            {/* MODAL CONTENT GOES HERE */}
            {props.children}
        {/*  </div>*/}
        {/*</div>*/}
      </Modal.Body>
      {props.footer ?
        <Modal.Footer className="px-3 py-3 m-a-modal-footer">
          {/*  MODAL FOOTER BUTTONS GOES HERE*/}
          {props.footer}
        </Modal.Footer>
        : null}
    </Modal>


  )
}

export default ModalComponent;