import React from 'react'
import "./AddedCard.scss";
import MoreOption from "../more-option/MoreOption";
import {
  sortableHandle,
} from 'react-sortable-hoc';

const AddedCard = (props) => {

  const moreOptionsData = [
    {
      name: "View Recommendations",
      action: () => alert("Clicked")
    },
    {
      name: "Remove",
      action: props.removeProductFromCartHandler
    }
  ]

  // Handle drag
  // TODO: Change Draggable icon
  const DragHandle = sortableHandle(() => <i className={["ml-4 drag-icon", props.disabledDrag ? "disabled-drag" : ""].join(" ")}></i>);

  return (
    <div className="added-card py-5">
      <div className="row">
        <div className="col-2">
          <img className="product-img d-block mx-auto" src={props.data?.item.imageUrl} alt={props.data?.item.title} />
        </div>
        <div className="col">
          <div className="text-sm category">{props.data.item.category.categoryName}</div>
          <h2 className="">{props.data.item.title}</h2>
          <div className="d-flex mt-2">
            <div className="">${props.data.item.price}</div>
            {/* TODO: Add Strikeout price dynamic */}
            <div className="text-sm my-auto ml-1 strick-price">$257.99</div>
            {props.data.quantity > 1 ?
              <div className="ml-4 text-body d-flex">
                Qty:{props.data.quantity}
                <div className="quantity-mul-text"> (${props.data.item.price} * {props.data.quantity})</div>
              </div> : <></>}
          </div>
        </div>
        <div className={[
          props.draggable ? "col-3" : "col-2",
        ].join(" ")}
        >
          <div className="d-flex align-items-baseline justify-content-end">
            <MoreOption data={moreOptionsData} iconClass="icon-more-hori" />
            <button
              className={[
                "icon",
                "ml-4",
                props.data.mostWanted ? "heart-fill" : "heart",
              ].join(" ")}
              onClick={props.toggleMustHave}
            />
            {/* TODO: Add icon/rearrange icon */}
            {props.draggable ?
              <DragHandle /> : <></>}
          </div>
        </div>
      </div>
    </div>
  )
}

export default AddedCard