import React, {useState, createRef} from "react";
import {useDispatch} from "react-redux";
import "./ShareRegistry.scss";
import Modal from "../../../commons/components/modal-component/ModalComponent";
import {UPDATE_REGISTRY, UPDATE_REGISTRY_URL} from "../../../../api/mutations";
import {graphQL} from "../../../../api/axios";
import {updateRegistryDetail} from "../../../../utils/commonFunction";
import {updateRegistryInfo} from "../../../registry/RegistryActions";
import Button from "../../../commons/components/button-component/ButtonComponent";

const ShareRegistry = (props) => {

  // TODO: Uncomment siteHostName for dynamic hostname
  // const siteHostName = window.location.host + "";
  const siteHostName = "monicaandandy.com";
  const dispatch = useDispatch()
  const [registryInfoUrl, setRegistryInfoUrl] = useState(props.registryInfo.urlSlug);
  const [registryVisibility, setRegistryVisibility] = useState(props.registryInfo.isPublic)
  const [registryUrl, setRegistryUrl] = useState(registryInfoUrl)
  const [registryError, setRegistryError] = useState("")
  const inputContainerRef = createRef();

  // Social media icons
  const facebookInactive = (
    <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="none" viewBox="0 0 36 36">
      <circle cx="18" cy="18" r="18" fill="#D2D3D5"/>
      <path fill="#fff"
            d="M22.012 18.934l.53-3.488h-3.374v-2.274c0-.986.455-1.896 1.972-1.896h1.554V8.281s-1.403-.265-2.73-.265c-2.767 0-4.587 1.706-4.587 4.739v2.691H12.27v3.488h3.108v8.492h3.791v-8.492h2.844z"/>
    </svg>
  )

  const instagramInactive = (
    <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="none" viewBox="0 0 36 36">
      <circle cx="18" cy="18" r="18" fill="#D2D3D5"/>
      <path fill="#fff"
            d="M17.5 13.361c-2.426 0-4.36 1.972-4.36 4.36 0 2.427 1.934 4.36 4.36 4.36 2.389 0 4.36-1.933 4.36-4.36 0-2.388-1.971-4.36-4.36-4.36zm0 7.203c-1.554 0-2.843-1.25-2.843-2.843 0-1.554 1.251-2.805 2.843-2.805 1.555 0 2.806 1.25 2.806 2.805 0 1.592-1.251 2.843-2.806 2.843zm5.535-7.354c0-.569-.455-1.024-1.023-1.024-.569 0-1.024.455-1.024 1.024 0 .568.455 1.023 1.024 1.023.568 0 1.023-.455 1.023-1.023zm2.881 1.023c-.075-1.365-.379-2.578-1.364-3.563-.986-.986-2.2-1.29-3.564-1.365-1.403-.076-5.61-.076-7.013 0-1.365.076-2.54.379-3.564 1.365-.986.985-1.289 2.198-1.365 3.563-.076 1.403-.076 5.611 0 7.014.076 1.365.38 2.54 1.365 3.564 1.024.985 2.199 1.288 3.564 1.364 1.402.076 5.61.076 7.013 0 1.365-.076 2.578-.379 3.564-1.364.985-1.024 1.289-2.2 1.364-3.564.076-1.403.076-5.611 0-7.014zm-1.82 8.492c-.265.759-.871 1.327-1.591 1.63-1.138.456-3.792.342-5.005.342-1.25 0-3.905.114-5.004-.341-.758-.304-1.327-.872-1.63-1.63-.455-1.1-.341-3.754-.341-5.005 0-1.213-.114-3.867.34-5.004.304-.72.873-1.29 1.631-1.592 1.1-.455 3.753-.342 5.004-.342 1.213 0 3.867-.113 5.005.342.72.265 1.289.872 1.592 1.592.455 1.137.341 3.791.341 5.004 0 1.251.114 3.905-.341 5.004z"/>
    </svg>
  )

  const twitterInactive = (
    <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="none" viewBox="0 0 36 36">
      <circle cx="18" cy="18" r="18" fill="#D2D3D5"/>
      <path fill="#fff"
            d="M26.696 13.778c.758-.569 1.44-1.251 1.971-2.047-.682.303-1.478.53-2.274.606.834-.493 1.44-1.25 1.744-2.199-.759.455-1.63.797-2.502.986-.759-.796-1.782-1.251-2.92-1.251-2.198 0-3.98 1.782-3.98 3.98 0 .304.038.607.114.91-3.299-.189-6.256-1.781-8.227-4.17-.341.569-.53 1.251-.53 2.01 0 1.364.681 2.578 1.78 3.298-.644-.038-1.288-.19-1.819-.493v.038c0 1.933 1.365 3.526 3.185 3.905-.304.076-.683.151-1.024.151-.265 0-.493-.037-.758-.075.493 1.592 1.971 2.73 3.715 2.767-1.365 1.062-3.07 1.706-4.928 1.706-.341 0-.645-.038-.948-.076 1.744 1.138 3.829 1.782 6.104 1.782 7.316 0 11.297-6.028 11.297-11.297v-.531z"/>
    </svg>
  )

  const facebookActive = (
    <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="none" viewBox="0 0 36 36">
      <circle cx="18" cy="18" r="18" fill="#3B5998"/>
      <path fill="#fff"
            d="M22.012 18.934l.53-3.488h-3.374v-2.274c0-.986.455-1.896 1.972-1.896h1.554V8.281s-1.403-.265-2.73-.265c-2.767 0-4.587 1.706-4.587 4.739v2.691H12.27v3.488h3.108v8.492h3.791v-8.492h2.844z"/>
    </svg>
  )

  const instagramActive = (
    <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="none" viewBox="0 0 36 36">
      <circle cx="18" cy="18" r="18" fill="#E1306C"/>
      <path fill="#fff"
            d="M17.5 13.361c-2.426 0-4.36 1.972-4.36 4.36 0 2.427 1.934 4.36 4.36 4.36 2.389 0 4.36-1.933 4.36-4.36 0-2.388-1.971-4.36-4.36-4.36zm0 7.203c-1.554 0-2.843-1.25-2.843-2.843 0-1.554 1.251-2.805 2.843-2.805 1.555 0 2.806 1.25 2.806 2.805 0 1.592-1.251 2.843-2.806 2.843zm5.535-7.354c0-.569-.455-1.024-1.023-1.024-.569 0-1.024.455-1.024 1.024 0 .568.455 1.023 1.024 1.023.568 0 1.023-.455 1.023-1.023zm2.881 1.023c-.075-1.365-.379-2.578-1.364-3.563-.986-.986-2.2-1.29-3.564-1.365-1.403-.076-5.61-.076-7.013 0-1.365.076-2.54.379-3.564 1.365-.986.985-1.289 2.198-1.365 3.563-.076 1.403-.076 5.611 0 7.014.076 1.365.38 2.54 1.365 3.564 1.024.985 2.199 1.288 3.564 1.364 1.402.076 5.61.076 7.013 0 1.365-.076 2.578-.379 3.564-1.364.985-1.024 1.289-2.2 1.364-3.564.076-1.403.076-5.611 0-7.014zm-1.82 8.492c-.265.759-.871 1.327-1.591 1.63-1.138.456-3.792.342-5.005.342-1.25 0-3.905.114-5.004-.341-.758-.304-1.327-.872-1.63-1.63-.455-1.1-.341-3.754-.341-5.005 0-1.213-.114-3.867.34-5.004.304-.72.873-1.29 1.631-1.592 1.1-.455 3.753-.342 5.004-.342 1.213 0 3.867-.113 5.005.342.72.265 1.289.872 1.592 1.592.455 1.137.341 3.791.341 5.004 0 1.251.114 3.905-.341 5.004z"/>
    </svg>

  )

  const twitterActive = (
    <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" fill="none" viewBox="0 0 36 36">
      <circle cx="18" cy="18" r="18" fill="#1DA1F2"/>
      <path fill="#fff"
            d="M26.696 13.778c.758-.569 1.44-1.251 1.971-2.047-.682.303-1.478.53-2.274.606.834-.493 1.44-1.25 1.744-2.199-.759.455-1.63.797-2.502.986-.759-.796-1.782-1.251-2.92-1.251-2.198 0-3.98 1.782-3.98 3.98 0 .304.038.607.114.91-3.299-.189-6.256-1.781-8.227-4.17-.341.569-.53 1.251-.53 2.01 0 1.364.681 2.578 1.78 3.298-.644-.038-1.288-.19-1.819-.493v.038c0 1.933 1.365 3.526 3.185 3.905-.304.076-.683.151-1.024.151-.265 0-.493-.037-.758-.075.493 1.592 1.971 2.73 3.715 2.767-1.365 1.062-3.07 1.706-4.928 1.706-.341 0-.645-.038-.948-.076 1.744 1.138 3.829 1.782 6.104 1.782 7.316 0 11.297-6.028 11.297-11.297v-.531z"/>
    </svg>
  )

  const closeModal = () => {
    setRegistryUrl(props.registryInfo.urlSlug)
    props.closeShareRegistry();
  }

  const changeRegistryVisibility = (value) => {
    if (value === registryVisibility) {
      return;
    }

    if (!props.registryInfo.id) {
      console.log("No registry found!");
      return;
    }

    setRegistryVisibility(value);

    let data = {
      query: UPDATE_REGISTRY,
      operationName: "updateRegistryDetail",
      variables: {
        "registryId": props.registryInfo.id,
        "input": {
          isPublic: value
        }
      }
    }

    graphQL(data).then(res => {
      try {
        if (res.data.data.updateRegistry.registry) {
          let newRegistry = props.registryInfo
          newRegistry.isPublic = value
          dispatch(updateRegistryInfo(newRegistry))
        } else {
          console.log(res.data.data.updateRegistry.errors);
          setRegistryVisibility(!value);
          // TODO: Replace this with error Toast
          console.error("Some error occurred");
        }
      } catch (e) {
        setRegistryVisibility(!value);
        // TODO: Replace this with error Toast
        console.error("Some error occurred");
        console.error(e);
      }
    }).catch(err => {
      setRegistryVisibility(!value);
      // TODO: Replace this with error Toast
      console.error("Some error occurred");
      console.error(err.message)
    })
  }

  const registryUrlChangeHandler = (ev) => {
    if (registryError) {
      setRegistryError("")
    }
    setRegistryUrl(ev.target.value);
  }

  const checkUrlAvailability = () => {
    if (registryUrl === props.registryInfo.urlSlug) {
      return
    }

    if (registryUrl.length <= 0) {
      // TODO: Add error toast
      console.log("Registry URL cannot be empty!");
      return;
    }

    let data = {
      query: UPDATE_REGISTRY_URL,
      operationName: "updateRegistryUrl",
      variables: {
        "registryId": props.registryInfo.id,
        "urlSlug": registryUrl
      }
    }

    graphQL(data).then(res => {
      try {
        if (res.data.data.checkUpdateRegistryUrlSlug.message) {
          let newRegistry = props.registryInfo
          newRegistry.urlSlug = registryUrl
          setRegistryInfoUrl(registryUrl);
          setRegistryError("")
          dispatch(updateRegistryInfo(newRegistry))
        } else {
          setRegistryError(`${registryUrl} is not available. Please try something else.`)
          console.log(res.data.data.checkUpdateRegistryUrlSlug.errors);
          // TODO: Replace this with error Toast
          console.error("Some error occurred");
        }
      } catch (e) {
        // TODO: Replace this with error Toast
        console.error("Some error occurred");
        console.error(e);
      }
    }).catch(err => {
      // TODO: Replace this with error Toast
      console.error("Some error occurred");
      console.error(err.message)
    })
  }

  const copyRegistryClickHandler = () => {
    const el = document.createElement('textarea');
    el.value = `${siteHostName}/${registryUrl}`;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
    // TODO: Add success toast
    console.log("Copied to clipboard");
  }

  const modalTitle = "Share Registry";
  const modalBody = (
    <>
      <div className="row">
        <div className="col d-flex flex-column">
          <span className="section-title-share-registry text-body font-medium">Status</span>
          <div className="visibility-status-btn mt-1">
            <button className={["text-sm p-2 mr-1", registryVisibility ? "active" : ""].join(" ")}
                    onClick={() => changeRegistryVisibility(true)}>Visible
            </button>
            <button className={["text-sm p-2", !registryVisibility ? "active" : ""].join(" ")}
                    onClick={() => changeRegistryVisibility(false)}>Secret
            </button>
          </div>
          <p
            className="text-sm mt-1">{registryVisibility ? "Guests can access this registry" : "Guests cant access this registry unless you set to visible."}</p>
        </div>
      </div>

      <div className="row mt-6 align-items-end">
        <div className="col d-flex flex-column">
          <span className="section-title-share-registry text-sm font-medium">Link</span>
          <div className="mt-1 d-flex align-items-baseline">
            <div>
              <div
                className={["registry-url-section text-body", registryError ? "error-input-container" : ""].join(" ")}
                ref={inputContainerRef}>
                <input
                  className="disabled-input-share-registry py-1 px-2"
                  value={siteHostName + "/"}
                  readOnly/>
                <input
                  className="registry-url-input py-1 px-2"
                  value={registryUrl}
                  onFocus={() => inputContainerRef.current.classList.add('focused-input-container')}
                  onBlur={() => inputContainerRef.current.classList.remove('focused-input-container')}
                  onChange={(ev) => registryUrlChangeHandler(ev)}
                />
              </div>
              {registryError ?
                <span className="text-sm input-error-message">{registryError}</span>
                : null}
            </div>
            {registryUrl !== registryInfoUrl ?
              <Button variant="primary" className="px-2 py-1 text-body font-medium ml-auto"
                      onClick={checkUrlAvailability}>Check</Button>
              :
              <Button variant="primary" className="px-2 py-1 text-body font-medium ml-auto"
                      onClick={copyRegistryClickHandler}>Copy</Button>
            }

          </div>
        </div>
      </div>

      <div className="row mt-10">
        <div className="col">
          <span className="text-sm font-medium section-title-share-registry">Share on Social Media</span>
          <div className="mt-2">
            {registryVisibility ?
              <>
                {/*TODO: Add click handler*/}
                <i className="mr-3">
                  {facebookActive}
                </i>
                <i className="mr-3">
                  {instagramActive}
                </i>
                <i>
                  {twitterActive}
                </i>
              </>
              :
              <>
                <i className="mr-3">
                  {facebookInactive}
                </i>
                <i className="mr-3">
                  {instagramInactive}
                </i>
                <i>
                  {twitterInactive}
                </i>
              </>
            }
          </div>
        </div>
      </div>
    </>
  );

  return (
    <Modal
      show={props.show}
      closeModal={closeModal}
      title={modalTitle}
      size="md"
    >
      {modalBody}
    </Modal>
  )
}

export default ShareRegistry;